import React from 'react';
import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFlushed } from '@fortawesome/free-regular-svg-icons';

const sound_lst = [
  {
    keyCode: 81,
    keyTrigger: 'Q',
    id: 'Chord-1',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Chord_1.mp3'
  },
  {
    keyCode: 87,
    keyTrigger: 'W',
    id: 'Chord-2',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3'
  },
  {
    keyCode: 69,
    keyTrigger: 'E',
    id: 'Chord-3',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Chord_3.mp3'
  },
  {
    keyCode: 65,
    keyTrigger: 'A',
    id: 'Shaker',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3'
  },
  {
    keyCode: 83,
    keyTrigger: 'S',
    id: 'Open-HH',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3'
  },
  {
    keyCode: 68,
    keyTrigger: 'D',
    id: 'Closed-HH',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3'
  },
  {
    keyCode: 90,
    keyTrigger: 'Z',
    id: 'Punchy-Kick',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3'
  },
  {
    keyCode: 88,
    keyTrigger: 'X',
    id: 'Side-Stick',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3'
  },
  {
    keyCode: 67,
    keyTrigger: 'C',
    id: 'Snare',
    url: 'https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3'
  }
];

export default class App extends React.Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="App">
        <Keys />
        <FontAwesomeIcon icon={faFlushed} />
      </div>
    );
  }
}

class Keys extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      key: "Hit or click a key",
    }
    this.Click = this.Click.bind(this);
  }

  componentDidMount() {
    create_sounds_in_background();
    document.addEventListener("keydown", function(event) {
      for (let sound of sound_lst) {
        if (event.keyCode === sound.keyCode) {
          let button = document.getElementById(sound.keyTrigger + "-pad");
          button.click();
          button.style["background-color"] = "black";
          button.style.color = "white";
          button.style["font-size"] = "calc(20px + 2vmin)";
          setTimeout(function(){ 
            button.style["background-color"] = null;
            button.style.color = null;
            button.style["font-size"] = null;
           }, 200);
          return;
        }
      }
    });
  }

  Click(clicked_key) {
    let audio = document.getElementById(clicked_key.target.id[0]);
    audio.pause();
    audio.currentTime = 0;
    audio.play();
    for (let sound of sound_lst) {
      if (sound.keyTrigger + "-pad" === clicked_key.target.id)
        {
          this.setState(
            {
              key: sound.id
            }
          );
          return;
        }
    }
  }

  render() {
    return (
    <div id="drum-machine">
      <div id="display1" className="window">
        <div className="header">keyboard drums</div>
        <div id="split-screen">
          <div id="left-side">
            <div className="key-grid">
              <button id="Q-pad" className="drum-pad" onClick={this.Click}>Q</button>
              <button id="W-pad" className="drum-pad" onClick={this.Click}>W</button>
              <button id="E-pad" className="drum-pad" onClick={this.Click}>E</button>
              <button id="A-pad" className="drum-pad" onClick={this.Click}>A</button>
              <button id="S-pad" className="drum-pad" onClick={this.Click}>S</button>
              <button id="D-pad" className="drum-pad" onClick={this.Click}>D</button>
              <button id="Z-pad" className="drum-pad" onClick={this.Click}>Z</button>
              <button id="X-pad" className="drum-pad" onClick={this.Click}>X</button>
              <button id="C-pad" className="drum-pad" onClick={this.Click}>C</button>
            </div>
          </div>
          <div id="right-side">
            <Output name={this.state.key}/>
          </div>
        </div>
      </div>
    </div>
    );
  }
};


const Output = (props) => {
  return (
    <div id="display">
      {props.name}
    </div>
  );
};

const create_sounds_in_background = () => {
  for (let sound of sound_lst) {
    let audio = document.createElement("AUDIO");
    audio.id = sound.keyTrigger;
    audio.className = "clip";
    audio.src = sound.url;
    audio.type = "audio/mpeg";
    let wrapper = document.getElementById(sound.keyTrigger + "-pad");
    wrapper.appendChild(audio);
  }
}